<template>
  <div class="container" id="component">

      <!-- Portfolio Section Heading -->
    <h2 class="page-section-heading text-center text-uppercase text-secondary mb-0">Ventas</h2>

    <!-- Icon Divider -->
    <div class="divider-custom">
      <div class="divider-custom-line"></div>
      <div class="divider-custom-icon">
        <i class="fas fa-star"></i>
      </div>
      <div class="divider-custom-line"></div>
    </div>

    <div class="row mb-5">
      <input type="text" autocomplete="off" class="form-control col-4" id="datepicker"/>
      <button class="btn btn-primary" @click="buscar()"> Buscar </button>

    </div>


    <table class="table table-bordered">
  <thead>
    <tr>
      <th scope="col">#</th>
      <th scope="col">Fecha</th>
      <th scope="col">Datos Cliente</th>
      <th scope="col">Items</th>
      <th scope="col">Envio</th>
      <th scope="col">Estado</th>
      <th scope="col">Valores</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="(item) of ventas" :key="item.uid">
      <td>{{item.uid}}</td>
      <td> {{item.fecha}} </td>
      <td> 
        Nombre: {{item.payer.name}} <br>
        Telefono: {{item.payer.phone.area_code}} - {{item.payer.phone.number}}
      </td>
      <td>
        <span v-for="(i) of item.items" :key="i.uid">
          <p> {{productos.find(e => e.uid == i.id).nombre}} - ({{productos.find(e => e.uid == i.id).tipo}}) <br>
         {{i.description}} <br v-if="i.description != ''">
          ${{i.unit_price}}</p>
        </span>
      </td>
      <td> 
        <div v-if="item.payer.address.zip_code != ''">
          Direccion: {{item.payer.address.street_name}} <br>
          Codigo Postal: {{item.payer.address.zip_code}}
        </div>
      </td>
      <td>{{item.payments[0].status}}</td>
      <td>
        <p>
          Pagado: ${{item.transaction_details.total_paid_amount}}
          </p> 
          <p>
          <!-- A Recibir: ${{item.transaction_details.net_received_amount}} -->
          </p>
          </td>
    </tr>
  </tbody>
</table>

  </div>
</template>

<script>
import { mapState, mapActions , mapGetters} from "vuex";

export default {
  name: 'Ventas',
  methods: {
    ...mapActions(['getVentas', 'getProductos']),
    buscar(){
      
      let fecha = $("#datepicker").val();
      if (fecha != ""){
        this.getVentas(fecha);
      }
    }
  },
  computed: {
    ...mapState(['error', 'ventas', 'productos'])
  },
  created(){
    this.getProductos();
    console.log(this.ventas)
  },
  mounted() {
    $("#datepicker")
      .datepicker()
      .datepicker("setDate", new Date());
  }
}
</script>