<template>
  <div class="container" id="component">

      <!-- Portfolio Section Heading -->
    <h2 class="page-section-heading text-center text-uppercase text-secondary mb-0">Imagenes</h2>

    <!-- Icon Divider -->
    <div class="divider-custom">
      <div class="divider-custom-line"></div>
      <div class="divider-custom-icon">
        <i class="fas fa-star"></i>
      </div>
      <div class="divider-custom-line"></div>
    </div>

    <!-- Portfolio Section -->
    <section id="portfolio">
      <div class="container">

        <button class="btn btn-dark mb-3" @click="btnAgregar= true" v-if="!btnAgregar">Agregar Imagen</button>

        <form v-if="btnAgregar" @submit.prevent="subirImagen(producto)">
          <div class="form-group row">
            <div class="col-12 mb-3">
              <img :src="producto.url ? producto.url : URLtemporal" class="img-thumbnail" style="width:150px"> 
            </div> 
              <div class="col-12">
              <button class="btn btn-danger" type="button" @click="$refs.boton.click()">Buscar Imagen</button>
            </div> 
          </div>
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">Nombre</label>
            <div class="col-sm-10">
              <input type="text" class="form-control" v-model="producto.nombre" required>
            </div>
          </div>
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">Descripcion</label>
            <div class="col-sm-10">
              <!-- <input type="text" class="form-control"  v-model="producto.descripcion"> -->
              <textarea class="form-control" rows="3" v-model="producto.descripcion" required></textarea>
            </div>
          </div>
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">Tipo</label>
            <div class="col-sm-10">
              <select class="form-control" v-model="producto.tipo" @change="limpiar" required>
                <option disabled value="">Seleccione un elemento</option>
                <option>Tazas</option>
                <option>Remeras</option>
                <option>Accesorios</option>
              </select>
            </div>
          </div>
          <div class="form-group row" v-if="producto.tipo == 'Remeras'">
            <label class="col-sm-2 col-form-label">Talles</label>
            <div class="col-sm-10">
            <div class="form-check form-check-inline">
              <input class="form-check-input" type="checkbox" value="S" v-model="producto.talles">
              <label class="form-check-label mr-2"> S </label>
              <input class="form-check-input" type="checkbox" value="M" v-model="producto.talles">
              <label class="form-check-label mr-2"> M </label>
              <input class="form-check-input" type="checkbox" value="L" v-model="producto.talles">
              <label class="form-check-label mr-2"> L </label>
              <input class="form-check-input" type="checkbox" value="XL" v-model="producto.talles">
              <label class="form-check-label mr-2"> XL </label>
              <input class="form-check-input" type="checkbox" value="XL" v-model="producto.talles">
              <label class="form-check-label mr-2"> XXL </label>
            </div>
            </div>
          </div>
          <div class="form-group row" v-if="producto.tipo == 'Remeras'">
            <label class="col-sm-2 col-form-label">Modelos</label>
            <div class="col-sm-10">
            <div class="form-check form-check-inline">
              <input class="form-check-input" type="checkbox" value="Blanca Lisa (Mujer)" v-model="producto.modelos">
              <label class="form-check-label mr-2"> Blanca Lisa (Mujer) </label>
              <input class="form-check-input" type="checkbox" value="Ranglan Gris (Mujer)" v-model="producto.modelos">
              <label class="form-check-label mr-2"> Ranglan Gris (Mujer) </label>
              <input class="form-check-input" type="checkbox" value="Ranglan Blanca (Mujer)" v-model="producto.modelos">
              <label class="form-check-label mr-2"> Ranglan Blanca (Mujer)</label>
              <input class="form-check-input" type="checkbox" value="Blanca Lisa (Hombre)" v-model="producto.modelos">
              <label class="form-check-label mr-2"> Blanca Lisa (Hombre) </label>
              <input class="form-check-input" type="checkbox" value="Ranglan Gris (Hombre)" v-model="producto.modelos">
              <label class="form-check-label mr-2"> Ranglan Gris (Hombre) </label>
              <input class="form-check-input" type="checkbox" value="Ranglan Blanca (Hombre)" v-model="producto.modelos">
              <label class="form-check-label mr-2"> Ranglan Blanca (Hombre) </label>
            </div>
            </div>
          </div>
          <div class="form-group row">
              <div class="col-sm-2">Lanzamiento</div>
              <div class="col-sm-10">
                <div class="form-check">
                  <input class="form-check-input" type="checkbox" v-model="producto.lanzamiento">
                  <label class="form-check-label">
                    {{(producto.lanzamiento)?'Activo':'Inactivo'}}
                  </label>
                </div>
              </div>
            </div>
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">Precio</label>
            <div class="col-sm-10">
              <input input type="number" min="1" step="any" class="form-control" v-model="producto.precio" required>
            </div>
          </div>
          <input type="file" ref="boton" class="d-none" @change="buscarImagen($event)">
          <button class="btn btn-warning col-2" type="submit" :disabled="file ===null && producto.url == ''" >
            <div class="spinner-grow spinner-grow-sm" role="status" v-if="loading">
              <span class="sr-only">Loading...</span>
            </div> 
            <div v-else>
              Guardar
            </div>
            <div>
            </div>
          </button>
          <button class="btn btn-warning col-2" type="button" @click="btnAgregar = !btnAgregar">Cancelar </button>
        </form>


        <table class="table" v-if="!btnAgregar">
          <thead class="thead-dark">
            <tr>
              <th scope="col"></th>
              <th scope="col">Nombre</th>
              <th scope="col">Tipo</th>
              <th scope="col">Precio</th>
              <th scope="col">Accion</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item) of productos.filter(e => e.eliminado != true)" :key="item.uid">
              <td><img :src="item.url" class="img-thumbnail" style="width:70px"></td>
              <td>{{item.nombre}}</td>
              <td>{{item.tipo}}</td>
              <td>{{item.precio}}</td>
              <td>
                <button class="col-12 btn btn-info  btn-sm" style="display: block !important;" @click="seleccionar(item);">Editar</button>
                <button class="col-12 btn btn-danger btn-sm" style="display: block !important;" @click="eliminarImagen(item.nombreFile); delProducto(item.uid)"  >Eliminar</button></td>
            </tr>
          </tbody>
        </table>
      </div>
    </section>

  </div>
</template>

<script>
import { mapState, mapActions , mapGetters} from "vuex";

import {firebase, storage, db} from "@/main";

export default {
  name: 'Imagenes',
  data(){
    return {
      // pantallaVentass: false,
      // pantallaImagenes: false,
      file : null,
      URLtemporal: 'img/sinImagen.png',
      loading: false,
      error: '',
      producto:{
        nombre:'',
        descripcion: '',
        tipo: '',
        talles: [],
        modelos: [],
        lanzamiento: true,
        precio:0,
        url:'',
        nombreFile:'',
        numero:0
      },
      btnAgregar: false,
      selected: ''
    }
  },
  methods: {
    ...mapActions(['getProductos','addProducto','delProducto','putProducto']),

    buscarImagen(event){
      const tipoArchivo = event.target.files[0].type

      if(tipoArchivo === 'image/jpeg' || tipoArchivo === 'image/png' || tipoArchivo === 'image/jpg'){
        this.file =event.target.files[0];
        this.error = ''
      } else{
        this.error = 'Archivo no valido'
        alert('Formato no Valido')
        this.file = null;
        return // asi no sigue
      }

      if(this.file != null){
        const reader = new FileReader()
        reader.readAsDataURL(this.file)
        reader.onload = (e) => {
          // traer URL temporal
          // console.log(e.target.result);
          this.URLtemporal = e.target.result

          this.producto.url = e.target.result
        }
      }
    },
    async subirImagen(){
      try {
        this.loading = true
        var me = this;

        // child se hacen carpetas puede ser por cada Usuario
        // const refImagen = storage.ref().child('img').child(this.file.name)
        if(this.file != null){

          if(this.file.name != me.producto.nombreFile){
            this.eliminarImagen(me.producto.nombreFile)
          }
          
          const refFiles = storage.ref().child('img')
          const refImagen = refFiles.child(this.file.name)
          const res = await refImagen.put(this.file)
          const url = await refImagen.getDownloadURL()

          me.producto.url = url
          me.producto.nombreFile = this.file.name
        }
        if(me.producto.uid == undefined){
          await db.collection('numProductos').doc('JoBCXYlC03kCnxS5Re5j').get()
          .then(doc => {
           me.producto.numero = parseInt(doc.data().id)+1
          })
          await this.addProducto(me.producto)
  
          await db.collection('numProductos').doc('JoBCXYlC03kCnxS5Re5j').update({
            id: me.producto.numero 
          })
        }else{
          await this.putProducto(me.producto)
        }
        await this.nuevo();

      } catch (error) {
        console.log(error);
      }
      finally{
        this.loading = false
        this.file = null;
        this.btnAgregar = false;
      }
    },
    async eliminarImagen(fileName){
      try {
        if(fileName != ''){
          const refFiles = storage.ref().child('img')
          const refImagen = refFiles.child(fileName)
          await refImagen.delete()
          .then(function() {
            console.log('File deleted successfully')
          })
          .catch(function(error) {
            console.log('Uh-oh, an error occurred!')
          });
        }
      } catch (error) {
        console.log(error);
      }
      finally{
        // this.fileAnterior = ''
      }
    },
    seleccionar(item){
      this.producto.uid = item.uid
      this.producto.nombre = item.nombre
      this.producto.descripcion = item.descripcion
      this.producto.tipo  = item.tipo
      this.producto.talles = item.talles
      this.producto.modelos  = item.modelos
      this.producto.lanzamiento = item.lanzamiento
      this.producto.precio = item.precio
      this.producto.url = item.url
      this.producto.nombreFile = item.nombreFile
      this.producto.numero = item.numero

      this.btnAgregar = true
    },
    limpiar(){
      this.producto.talles = []
      this.producto.modelos = []
    },
    nuevo(){
      this.file = null
      this.URLtemporal= 'img/sinImagen.png'
      this.loading= false
      this.error= ''
      this.producto = {
        nombre:'',
        descripcion: '',
        tipo: '',
        talles: [],
        modelos: [],
        lanzamiento: true,
        precio:0,
        url:'',
        nombreFile:'',
        numero:0
      }
    }
  },
  computed: {
    ...mapState(['productos','usuario'])
  },
  created() {
   
  },
  mounted() {
    this.getProductos()
  }
}
</script>