<template>
  <div class="container" id="component">

      <!-- Portfolio Section Heading -->
    <h2 class="page-section-heading text-center text-uppercase text-secondary mb-0">Banners</h2>

    <!-- Icon Divider -->
    <div class="divider-custom">
      <div class="divider-custom-line"></div>
      <div class="divider-custom-icon">
        <i class="fas fa-star"></i>
      </div>
      <div class="divider-custom-line"></div>
    </div>

    <!-- Portfolio Section -->
    <section id="portfolio">
      <div class="container">

        <button class="btn btn-dark mb-3" @click="btnAgregar= true" v-if="!btnAgregar">Agregar Imagen</button>

        <form v-if="btnAgregar" @submit.prevent="subirImagen(banner)">
          <div class="form-group row">
            <div class="col-12 mb-3">
              <img :src="URLtemporal" class="img-thumbnail" style="width:150px"> 
            </div> 
              <div class="col-12">
              <button class="btn btn-danger" type="button" @click="$refs.boton.click()">Buscar Imagen</button>
            </div> 
          </div>
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">Nombre</label>
            <div class="col-sm-10">
              <input type="text" class="form-control" v-model="banner.nombre" required>
            </div>
          </div>
          <div class="form-group row">
            <label class="col-sm-2 col-form-label">Tipo</label>
            <div class="col-sm-10">
              <select class="form-control" v-model="banner.tipo" required>
                <option disabled value="">Seleccione un elemento</option>
                <option>Celular</option>
                <option>Pagina</option>
              </select>
            </div>
          </div>
          <div class="form-group row">
              <div class="col-sm-2">Activo</div>
              <div class="col-sm-10">
                <div class="form-check">
                  <input class="form-check-input" type="checkbox" v-model="banner.activo">
                  <label class="form-check-label">
                    {{(banner.activo)?'Activo':'Inactivo'}}
                  </label>
                </div>
              </div>
            </div>
          <input type="file" ref="boton" class="d-none" @change="buscarImagen($event)">
          <button class="btn btn-warning col-2" type="submit" :disabled="file ===null" >
            <div class="spinner-grow spinner-grow-sm" role="status" v-if="loading">
              <span class="sr-only">Loading...</span>
            </div> 
            <div v-else>
              Guardar
            </div>
            <div>
            </div>
          </button>
          <button class="btn btn-warning col-2" type="button" @click="btnAgregar = !btnAgregar">Cancelar </button>
        </form>


        <table class="table" v-if="!btnAgregar">
          <thead class="thead-dark">
            <tr>
              <th scope="col"></th>
              <th scope="col">Nombre</th>
              <th scope="col">Tipo</th>
              <th scope="col">Estado</th>
              <th scope="col">Accion</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item) of banners" :key="item.uid">
              <td><img :src="item.url" class="img-thumbnail" style="width:70px"></td>
              <td>{{item.nombre}}</td>
              <td>{{item.tipo}}</td>
              <td>{{item.activo}}</td>
              <td>
                <button class="col-12 btn btn-info  btn-sm" style="display: block !important;" >Editar</button>
                <button class="col-12 btn btn-danger btn-sm" style="display: block !important;" @click="delBanner(item.uid)"  >Eliminar</button></td>
            </tr>
          </tbody>
        </table>
      </div>
    </section>

  </div>
</template>

<script>
import { mapState, mapActions , mapGetters} from "vuex";

import {firebase, storage, db} from "@/main";

export default {
  name: 'Banners',
  data(){
    return {
    //   pantallaVentass: false,
    //   pantallaImagenes: false,
      file : null,
      URLtemporal: 'img/sinImagen.png',
      loading: false,
      error: '',
      banner:{
        nombre:'',
        tipo: '',
        activo: true,
        url:''
      },
      btnAgregar: false,
      selected: ''
    }
  },
  methods: {
    ...mapActions(['getBanners','addBanner','delBanner']),

    buscarImagen(event){
      const tipoArchivo = event.target.files[0].type

      if(tipoArchivo === 'image/jpeg' || tipoArchivo === 'image/png' || tipoArchivo === 'image/jpg'){
        this.file =event.target.files[0];
        this.error = ''
      } else{
        this.error = 'Archivo no valido'
        alert('Formato no Valido')
        this.file = null;
        return // asi no sigue
      }

      if(this.file != null){
        const reader = new FileReader()
        reader.readAsDataURL(this.file)
        reader.onload = (e) => {
          // traer URL temporal
          // console.log(e.target.result);
          this.URLtemporal = e.target.result

          this.banner.url = e.target.result
        }
      }
    },
    async subirImagen(){
      try {
        this.loading = true
        var me = this;

        // child se hacen carpetas puede ser por cada Usuario
        // const refImagen = storage.ref().child('img').child(this.file.name)
        const refFiles = storage.ref().child('banner')
        const refImagen = refFiles.child(this.file.name)
        const res = await refImagen.put(this.file)

        const url = await refImagen.getDownloadURL()

          me.banner.url = url
          me.banner.nombreFile = this.file.name

        await this.addBanner(me.banner)

        await this.nuevo();

      } catch (error) {
        console.log(error);
      }
      finally{
        this.loading = false
        this.file = null;
        this.btnAgregar = false;
      }
    },
    nuevo(){
      this.file = null
      this.URLtemporal= 'img/sinImagen.png'
      this.loading= false
      this.error= ''
      this.banner = {
        nombre:'',
        tipo: '',
        activo: true,
        url:''
      }
    }
  },
  computed: {
    ...mapState(['banners','usuario'])
  },
  created() {
   
  },
  mounted() {
    this.getBanners()
  }
}
</script>