<template>
  <div class="container" id="component">

      <!-- Portfolio Section Heading -->
    <h2 class="page-section-heading text-center text-uppercase text-secondary mb-0">Login</h2>

    <!-- Icon Divider -->
    <div class="divider-custom">
      <div class="divider-custom-line"></div>
      <div class="divider-custom-icon">
        <i class="fas fa-star"></i>
      </div>
      <div class="divider-custom-line"></div>
    </div>

    <form @submit.prevent="ingresar({email, pass})">
        <div class="form-group">
            <label for="exampleInputEmail1">Email address</label>
            <input type="email" class="form-control" v-model="email" id="exampleInputEmail1" aria-describedby="emailHelp">
        </div>
        <div class="form-group">
            <label for="exampleInputPassword1">Password</label>
            <input type="password" class="form-control" v-model="pass" id="exampleInputPassword1">
        </div>
        <button type="submit" class="btn btn-primary">Ingresar</button>
    </form>

    {{error}}
  </div>
</template>

<!-- Custom scripts for this template -->
<script src="../../js/freelancer.min.js"></script>

<script>
import { mapState, mapActions , mapGetters} from "vuex";

export default {
  name: 'Login',
  data(){
    return {
      email:'m@m.com',
      pass: 'm@m.com'
    }
  },
  methods: {
    ...mapActions(['ingresoUsuario']),
    async ingresar(data){
      await this.ingresoUsuario({email:data.email, pass:data.pass})
    }
  },
  computed: {
    ...mapState(['error', 'usuario'])
    // ...mapGetters([''])
  }
}
</script>